/* eslint-disable prettier/prettier */
import {
  listArticle,
  writeArticle,
  getArticle,
  modifyArticle,
  deleteArticle,
} from "@/api/board.js";

const boardStore = {
  namespaced: true,
  state: {
    articles: [],
    article: {},
    previousArticle: {},
    nextArticle: {},
    isSuccess: false,
    totalRows: 0,
    currentPage: Number,
  },
  getters: {
    articles(state) {
      return state.articles;
    },
    article(state) {
      return state.article;
    },
    isSuccess(state) {
      return state.isSuccess;
    },
    totalCount(state) {
      return state.totalRows;
    },
    getCurrentPage(state) {
      return state.currentPage;
    },
    previousArticle(state) {
      console.log(state.previousArticle);
      return state.previousArticle;
    },
    nextArticle(state) {
      return state.nextArticle;
    },
  },
  mutations: {
    setArticles(state, payload) {
      state.articles = payload;
    },
    setArticle(state, payload) {
      state.article = payload;
      state.isSuccess = true;
    },
    setPreviousArticle(state, payload) {
      console.log("5");
      console.log(payload);
      state.previousArticle = payload;
    },
    setNextArticle(state, payload) {
      state.nextArticle = payload;
    },
    setClearArticle(state, payload) {
      state.previousArticle = payload;
      state.nextArticle = payload;
    },
    setSuccess(state, payload) {
      state.isSuccess = payload;
    },
    setTotalRows(state, payload) {
      state.totalRows = payload;
    },
    setCurrentPage(state, payload) {
      state.currentPage = payload;
    },
  },
  actions: {
    // 서버에서 게시글을 얻고 mutation의 setArticles를 호출한다.
    getArticles: ({ commit }, param) => {
      listArticle(
        param,
        ({ data }) => {
          commit("setTotalRows", data.pageNav.totalCount);
          commit("setArticles", data.list);
          // commit("setCurrentPage", param.pg);
        },
        (error) => {
          console.log(error);
        }
      );
    },
    getArticle: ({ commit }, articleno) => {
      getArticle(
        articleno,
        ({ data }) => {
          commit("setArticle", data);
        },
        (error) => {
          console.log(error);
        }
      );
    },
    async getPreviousArticle({ commit }, articleno) {
      console.log("3");
      await getArticle(
        articleno,
        ({ data }) => {
          console.log("4");
          console.log(data);
          commit("setPreviousArticle", data);
        },
        (error) => {
          console.log(error);
        }
      );
    },
    async getNextArticle({ commit }, articleno) {
      console.log("3");
      await getArticle(
        articleno,
        ({ data }) => {
          console.log("4");
          commit("setNextArticle", data);
        },
        (error) => {
          console.log(error);
        }
      );
    },
    async writeArticle({ commit }, article) {
      await writeArticle(
        article,
        ({ data }) => {
          console.log(data);
          commit("setArticle", article);
        },
        (error) => {
          console.log(error);
        }
      );
    },
    async modifyArticle({ commit }, article) {
      await modifyArticle(
        article,
        ({ data }) => {
          commit("setArticle", article);
          let msg = "수정 처리시 문제가 발생했습니다.";
          if (data === "success") {
            msg = "수정이 완료되었습니다.";
          }
          alert(msg);
        },
        (error) => {
          console.log(error);
        }
      );
    },
    async deleteArticle({ commit }, articleno) {
      await deleteArticle(
        articleno,
        ({ data }) => {
          commit("setArticle", {});
          let msg = "삭제 처리시 문제가 발생했습니다.";
          if (data === "success") {
            msg = "삭제가 완료되었습니다.";
          }
          alert(msg);
        },
        (error) => {
          console.log(error);
        }
      );
    },
  },
};

export default boardStore;
